import React from 'react'
import { connect } from 'react-redux'

class SetupPage extends React.Component {

	constructor(props) {
	    super(props);
	}

	render() {
		return (

			<div style={style}>
			  	<Typography variant="display1" gutterBottom>
			    	Set Up a Session    
		      	</Typography>
		      	<br/>
			  	<Dropdown />
			  	<br/>
			  	<InputK/>
			  	<br/>
			  	<InputB/>
			  	<br/>
			  	<RunButton/>
			  </div>

		)
	}
}


function mapStateToProps(state) {
  return {}
}

export default connect(
  mapStateToProps,
  {}
)(App);
